import React, { useEffect, useState } from 'react';
import './App.scss';
import './styles/general.scss';
import { PostsList } from './components/PostsList';
import { PostDetails } from './components/PostDetails';
import { getAllPosts, getUserPosts } from './api/posts';
import { Loader } from './components/Loader';
import { getAllUsers } from './api/users';

const App: React.FC = () => {
  const [users, setUsers] = useState<User[] | null>([]);
  const [posts, setPosts] = useState<Post[]>([]);
  const [selectedPostId, setSelectedPostId] = useState(0);
  const [postsLoaded, setPostsLoaded] = useState(false);

  useEffect(() => {
    const loadUsers = async () => {
      const usersFromServer = await getAllUsers();

      setUsers(usersFromServer);
    };

    loadUsers();
  }, []);

  useEffect(() => {
    const loadPosts = async () => {
      const postsFromServer = await getAllPosts();

      setPostsLoaded(true);

      setPosts(postsFromServer);
    };

    loadPosts();
  }, []);

  const handlePostId = (postId: number) => {
    if (selectedPostId === postId) {
      setSelectedPostId(0);
    } else {
      setSelectedPostId(postId);
    }
  };

  async function changeUser(userId: number) {
    const userPosts = await getUserPosts(userId);

    setPosts(userPosts);
  }

  return (
    <div className="App">
      <header className="App__header">
        <label>
          Select a user: &nbsp;

          <select
            className="App__user-selector"
            onChange={(event) => changeUser(Number(event.target.value))}
          >
            <option value="0">All users</option>
            {users?.map((user, index) => {
              if (index <= 8) {
                return (
                  <option
                    key={user.id}
                    value={user.id}
                  >
                    {user.name}
                  </option>
                );
              }

              return null;
            })}
          </select>
        </label>
      </header>

      <main className="App__main">
        <div className="App__sidebar">
          {postsLoaded
            ? (
              <PostsList
                posts={posts}
                selectedPostId={selectedPostId}
                handlePostId={handlePostId}
              />
            )
            : <Loader />}
        </div>

        <div className="App__content">
          {selectedPostId !== 0 && (
            <PostDetails
              selectedPostId={selectedPostId}
            />
          )}

        </div>
      </main>
    </div>
  );
};

export default App;
